@charset "UTF-8";
* {
  font-family: "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
}

body {
  color: #573001;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1.6em;
}

a img {
  opacity: 1;
  transition: opacity 0.4s;
}
a:hover img {
  opacity: 0.8;
}
a.opacity {
  opacity: 1;
  transition: opacity 0.4s;
}
a.opacity:hover {
  opacity: 0.8;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}

.center-block {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.center {
  text-align: center;
}

.flex {
  display: flex;
}

.f-wrap {
  flex-wrap: wrap;
}

.f-jc-sb {
  justify-content: space-between;
}

.f-jc-c {
  justify-content: center;
}

.f-jc-fe {
  justify-content: flex-end;
}

.f-ai-c {
  align-items: center;
}

.f-ai-fs {
  align-items: flex-start;
}

.f-ai-fe {
  align-items: flex-end;
}

.f-ac-c {
  align-content: center;
}

.base {
  width: 100%;
  max-width: 1387px;
  margin: 0 auto;
  padding: 0 25px;
}

.header {
  font-family: "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, Osaka, "ＭＳ Ｐゴシック", "MS PGothic", sans-serif;
  width: 174px;
}
.header__logo {
  width: 100%;
  margin-bottom: 60px;
}
.header__navi li {
  margin-bottom: 40px;
  text-align: center;
}
.header__navi li:last-child {
  margin-bottom: 0;
}
.header__navi li a {
  font-size: 14px;
  color: #40332a;
  font-weight: 700;
  display: block;
  letter-spacing: 0.03em;
}
.header__navi li a span {
  font-size: 10px;
  color: #B52310;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  display: block;
  transform: scale(0.8);
  letter-spacing: 0.1em;
  margin-top: 5px;
}
@media only screen and (min-width: 1100px) {
  .header.scroll {
    padding: 15px 0;
  }
  .header .sp-nav {
    display: none !important;
  }
}
@media only screen and (max-width: 1099px) {
  .header {
    display: flex;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 25px 0 38px;
    box-sizing: content-box;
  }
  .header__inner {
    display: block;
  }
  .header__logo {
    width: 80px;
    margin-bottom: 0;
  }
  .header__gnav {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: auto;
    display: block;
    opacity: 0;
    visibility: hidden;
    z-index: -9999;
    transition: all 0.4s;
    margin: 0;
    padding-bottom: 20px;
    background: #fff;
  }
  .header__gnav.active {
    opacity: 1;
    visibility: visible;
    z-index: 999999;
  }
  .header__navi {
    display: block;
    text-align: center;
    margin-bottom: 25px;
    padding-top: 70px;
  }
  .header__navi li {
    margin: 0 0 25px;
  }
  .header__navi li:last-child {
    margin-bottom: 0;
  }
  .header__navi li a {
    font-size: 16px;
    letter-spacing: 0.21em;
  }
  .header .nav__toggle {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 42px;
    height: 60px;
    position: fixed;
    top: 45px;
    right: 20px;
    z-index: 9999999;
    cursor: pointer;
  }
  .header .nav__toggle div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 42px;
    aspect-ratio: 1;
    gap: 5px;
    background-color: #40332A;
    border-radius: 50%;
    position: relative;
  }
  .header .nav__toggle div span {
    display: block;
    background: #fff;
    height: 2px;
    width: 16px;
    border-radius: 1px;
    transition: transform 0.3s;
  }
  .header .nav__toggle.opened span {
    position: absolute;
    left: 50%;
    translate: -50% 0;
  }
  .header .nav__toggle.opened span:nth-child(1) {
    top: 48%;
    transform: rotate(45deg);
  }
  .header .nav__toggle.opened span:nth-child(2) {
    bottom: 48%;
    transform: rotate(-45deg);
  }
  .header .pc-nav {
    display: none !important;
  }
}
@media screen and (max-width: 768px) {
  .header {
    justify-content: space-between;
    padding-right: 72px;
    height: 120px;
  }
  .header__logo {
    width: 139px;
  }
  .header .nav__toggle {
    top: 55px;
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 86px;
  text-align: center;
  background-color: #FFEF51;
  position: relative;
}
@media screen and (max-width: 768px) {
  .footer {
    height: 70px;
  }
}

.copyright {
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 0.12em;
}
@media screen and (max-width: 768px) {
  .copyright {
    font-size: 9px;
  }
}

.pagetop {
  display: flex;
  justify-content: center;
  width: 30px;
  position: absolute;
  z-index: 10;
  right: 40px;
  bottom: 120px;
}
@media only screen and (max-width: 1099px) {
  .pagetop {
    right: 20px;
  }
}
@media screen and (max-width: 768px) {
  .pagetop {
    width: 66px;
    right: 50%;
    bottom: auto;
    top: -67px;
    translate: 50% 0;
  }
}

@media screen and (min-width: 769px) {
  .sp {
    display: none !important;
  }
}
@media screen and (max-width: 768px) {
  .pc {
    display: none !important;
  }
}